import { setScore, setCoefficients, setTotalScore } from './actions';
export const checkAndSetScore = (isWin, prize) => (dispatch, getState) => {
  const {
    score
  } = getState();
  const newScore = [score[0], score[1]];
  newScore[isWin ? 0 : 1] += 1;
  dispatch(setScore(newScore));

  if (newScore.includes(10)) {
    const {
      totalScore
    } = getState();
    const newTotalScore = [totalScore[0], totalScore[1]];
    const scoringIndex = isWin ? 0 : 1;
    newTotalScore[scoringIndex] = totalScore[scoringIndex] + prize;
    dispatch(setTotalScore(newTotalScore));
    dispatch(setCoefficients([0, 0]));
    setTimeout(() => {
      dispatch(setScore([0, 0]));
    }, 800);
  }
};
export const checkAndSetCoefficients = isWin => (dispatch, getState) => {
  const {
    coefficients
  } = getState();
  const newCoefficients = [coefficients[0], coefficients[1]];
  newCoefficients[isWin ? 0 : 1] += 1;
  newCoefficients[!isWin ? 0 : 1] = 0;
  dispatch(setCoefficients(newCoefficients));
};
export const computeAndSetTotalScore = (isWin, scoreNum) => (dispatch, getState) => {
  const {
    coefficients,
    totalScore
  } = getState();
  const newTotalScore = [totalScore[0], totalScore[1]];
  const scoringIndex = isWin ? 0 : 1;
  const coefficient = coefficients[scoringIndex] > 0 ? coefficients[scoringIndex] : 1;
  newTotalScore[scoringIndex] += coefficient * scoreNum;
  dispatch(setTotalScore(newTotalScore));
};