import styled from 'react-emotion';
const StyledBall = styled('img')`
  position: absolute;
  left: ${({
  chosenPosition
}) => {
  if (chosenPosition === "left") {
    return '59px';
  }

  if (chosenPosition === "center") {
    return '297px';
  }

  return '533px';
}};
  bottom: 20px;
`;
export default StyledBall;