import styled, { keyframes } from 'react-emotion';
const viewKeyframes = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`;
export const View = styled('img')`
  position: absolute;
  left: 50%;
  top: 144px;
  z-index: 2;

  margin-left: -52px;
  animation: ${({
  emotion
}) => emotion === 'default' ? `${viewKeyframes} 0.3s steps(2) infinite` : ''};
  animation-delay: 0.2s;

  opacity: 0;
`;
const headDefaultKeyframes = keyframes`
  from {
    top: 0;
  }
  50% {
    top: 10px;
  }
  to {
    top: 0;
  }
`;
const headHappyKeyframes = keyframes`
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  to {
    transform: scale(1);
  }
`;
const headUnhappyKeyframes = keyframes`
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  to {
    transform: scale(1);
  }
`;
export const StyledWrapper = styled('div')`
  position: relative;
  left: ${({
  position
}) => {
  if (position === 'left') {
    return '-236px';
  }

  if (position === 'right') {
    return '236px';
  }

  return '0';
}};

  margin-bottom: 85px;

  transition: left 0.2s ease;
  animation: ${({
  emotion
}) => {
  if (emotion === 'default') {
    return `${headDefaultKeyframes} 1s ease infinite`;
  }

  if (emotion === 'happy') {
    return `${headHappyKeyframes} 0.4s ease`;
  }

  if (emotion === 'unhappy') {
    return `${headUnhappyKeyframes} 0.4s ease`;
  }

  return '';
}};
`;