import * as React from 'react';
import { StyledWrapper, StyledNum } from './styles';

class Score extends React.PureComponent {
  constructor(props) {
    super(props);
  }

  getGameState(score) {
    const diff = score[0] - score[1];

    switch (diff === 0 ? 0 : diff / Math.abs(diff)) {
      case 1:
        return "user_lead";
        break;

      case -1:
        return "random_lead";
        break;

      case 0:
        return "draw";
        break;

      default:
        return "draw";
    }
  }

  render() {
    const {
      score
    } = this.props;
    const gameState = this.getGameState(score);
    return React.createElement(StyledWrapper, null, React.createElement(StyledNum, {
      score: score[0],
      gameState: gameState,
      user: true
    }), React.createElement(StyledNum, {
      score: score[1],
      gameState: gameState
    }));
  }

}

export default Score;