import * as React from 'react';
import { StyledWrapper, View } from './Head.styles';
import '../../static/head-default.svg';
import '../../static/head-happy.svg';
import '../../static/head-unhappy.svg';
import '../../static/view.svg';

class Head extends React.PureComponent {
  constructor(props) {
    super(props);
    this.getEmotion = this.getEmotion.bind(this);
  }

  getEmotion() {
    switch (this.props.isWin) {
      case true:
        return "happy";
        break;

      case false:
        return "unhappy";
        break;

      case null:
      default:
        return "default";
    }
  }

  render() {
    const {
      position,
      isMouseEnter
    } = this.props;
    const emotion = this.getEmotion();
    return React.createElement(StyledWrapper, {
      position: position,
      emotion: emotion
    }, isMouseEnter && React.createElement(View, {
      src: "./images/view.svg",
      emotion: emotion,
      alt: "view"
    }), React.createElement("img", {
      src: `./images/head-${emotion}.svg`,
      alt: "head"
    }));
  }

}

export default Head;