import * as React from 'react';
import styled, { css } from 'react-emotion';
const NumCSS = css`
  font-family: 'Helvetica Neu', Helvetica, Arial;
  font-size: 36px;
  text-align: center;
  position: relative;
  display: inline-block;
  width: 64px;
  box-sizing: border-box;
  border-radius: 50%;

  transition: left 0.2s ease;
`;

const Num = ({
  className,
  score
}) => React.createElement("div", {
  className: `${NumCSS} ${className}`
}, score);

export const StyledNum = styled(Num)`
  line-height: ${({
  user
}) => user ? '60px' : '64px'};

  left: ${({
  gameState,
  user
}) => {
  if (gameState === 'draw') {
    return user ? '1px' : '-1px';
  }

  return user ? '5px' : '-5px';
}};
  z-index: ${({
  gameState,
  user
}) => {
  if (gameState === 'draw') {
    return '';
  }

  if (user) {
    return gameState === 'user_lead' ? '1' : '0';
  }

  return gameState === 'random_lead' ? '1' : '0';
}};

  background-color: ${({
  user
}) => user ? '#FCFFDA' : '#49404E'};
  color: ${({
  user
}) => user ? '#49404E' : '#FCFFDA'};
  border: ${({
  user
}) => user ? '2px solid #49404E' : ''};
`;
export const StyledWrapper = styled('div')`
  position: absolute;
  top: 35px;
  left: 35px;
  z-index: 1;
`;