import * as React from 'react';
import shuffle from 'lodash-es/shuffle';
import StyledWrapper from './styles';
import Head from './Head';
import Shells from './Shells';

class Game extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      position: "center",
      isMouseEnter: false,
      chosenPosition: null,
      isWin: null
    };
    this.move = this.move.bind(this);
    this.onChoose = this.onChoose.bind(this);
    this.onMouseLeave = this.onMouseLeave.bind(this);
    this.updateStore = this.updateStore.bind(this);
  }

  move(position) {
    this.setState({
      position,
      isMouseEnter: true
    });
  }

  random() {
    const position = shuffle(["left", "center", "right"]);
    const oneOrTwo = Math.floor(Math.random() * 4 + 1) === 3 ? 2 : 1;
    return Array.apply(null, {
      length: oneOrTwo
    }).map(() => position.splice(0, 1)[0]);
  }

  onChoose(position) {
    const winningPositions = this.random();
    const isWin = winningPositions.includes(position);
    this.setState({
      chosenPosition: position,
      isWin
    });
    setTimeout(() => {
      this.setState({
        chosenPosition: null,
        isWin: null
      });
    }, 800);
    this.updateStore(isWin);
  }

  updateStore(isWin) {
    const {
      setScore,
      setTotalScore,
      setCoefficients
    } = this.props;
    setScore(isWin, Game.PRIZE);
    setTotalScore(isWin, Game.SCORE_NUM);
    setCoefficients(isWin);
  }

  onMouseLeave() {
    this.setState({
      isMouseEnter: false
    });
  }

  render() {
    const {
      position,
      isMouseEnter,
      chosenPosition,
      isWin
    } = this.state;
    return React.createElement(StyledWrapper, null, React.createElement(Head, {
      isWin: isWin,
      position: position,
      isMouseEnter: isMouseEnter
    }), React.createElement(Shells, {
      move: this.move,
      onChoose: this.onChoose,
      onMouseLeave: this.onMouseLeave,
      chosenPosition: chosenPosition,
      isWin: isWin
    }));
  }

}

Game.SCORE_NUM = 50;
Game.PRIZE = 500;
export default Game;