import { connect } from 'react-redux';
import Game from '.';
import { checkAndSetScore, checkAndSetCoefficients, computeAndSetTotalScore } from '../../store/thunk-actions';

const mapDispatchToProps = dispatch => ({
  setScore: (isWin, prize) => dispatch(checkAndSetScore(isWin, prize)),
  setCoefficients: isWin => dispatch(checkAndSetCoefficients(isWin)),
  setTotalScore: (isWin, scoreNum) => dispatch(computeAndSetTotalScore(isWin, scoreNum))
});

export default connect(undefined, mapDispatchToProps)(Game);